.futureChallenge {
  display: inline-block;
  margin: 50px;
  a {
    text-align: center;
    display: block;
  }
  img {
    width: 60%;
    height: 60%;
    border-radius: 5px;
  }
}

.challenges {
  ul {
    list-style: none;
  }
}

.currentChallenge {
  text-align: center;
  img {
    width: 100%;
    border-radius: 5px;
  }
  a {
    display: block;
  }
}

.mainPopUp {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  background-color: rgba(0,0,0,0.5);
  .inner {
    background-color: white;
    color: black;
    width: 400px;
    height: 500px;
    z-index: 1;
    button {
      color: black !important;
    }
  }
}

.google-map {
  width: 100%;
  height: 300px;
  margin-top: 10px;
  margin-bottom: 10px;
}

.profileImage {
  border-radius: 60px;
  width: 100px;
  height: 100px;
}

.addChallenge {
  width: auto;
  margin-left: 100%;
  margin-bottom: 50px;
}















